@import url("https://fonts.googleapis.com/css?family=Baloo+Bhaina+2&display=swap");
@import url("https://fonts.googleapis.com/css?family=Do+Hyeon&display=swap");

* {
	font-family: "Baloo", "Do Hyeon";
}

.app-container {
	height: 100vh;

	display: flex;
	justify-content: center;

	background-color: rgb(255, 230, 177);
}

.container {
	margin: 1%;
	padding: 1%;
	width: 40%;
	height: 90%;

	background-color: white;

	box-shadow: 1px 1px 5px grey;

	justify-content: center;
	align-items: center;
	display: flex;
	flex-direction: column;
}

.header {
	height: 20%;
	width: 93%;

	background-color: #2c3e50;
}

.body {
	width: 100%;
	height: 100vh;
}

.footer {
	height: 7px;
	position: sticky;
	bottom: 0;

	justify-content: center;
	align-items: center;
}

.footer-title {
	color: grey;
}

@media only screen and (max-width: 800px) {
	.container {
		width: 80%;
	}

	.footer {
		margin: 3%;
	}
}

@media only screen and (min-width: 800px) and (max-width: 1600px) {
	.container {
		width: 60%;
	}
}
