@import url(https://fonts.googleapis.com/css?family=Baloo+Bhaina+2&display=swap);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon&display=swap);
* {
	font-family: "Baloo", "Do Hyeon";
}

.app-container {
	height: 100vh;

	display: flex;
	justify-content: center;

	background-color: rgb(255, 230, 177);
}

.container {
	margin: 1%;
	padding: 1%;
	width: 40%;
	height: 90%;

	background-color: white;

	box-shadow: 1px 1px 5px grey;

	justify-content: center;
	align-items: center;
	display: flex;
	flex-direction: column;
}

.header {
	height: 20%;
	width: 93%;

	background-color: #2c3e50;
}

.body {
	width: 100%;
	height: 100vh;
}

.footer {
	height: 7px;
	position: -webkit-sticky;
	position: sticky;
	bottom: 0;

	justify-content: center;
	align-items: center;
}

.footer-title {
	color: grey;
}

@media only screen and (max-width: 800px) {
	.container {
		width: 80%;
	}

	.footer {
		margin: 3%;
	}
}

@media only screen and (min-width: 800px) and (max-width: 1600px) {
	.container {
		width: 60%;
	}
}

.header-container {
  width: 100%;
  padding: 3%;

  color: white;

  align-items: center;
}

.hedaer- {
  font-size: 25px;
}

.pic-container {
  width: 100px;
  height: 100px;
}

.pic-img {
  border-radius: 20px;
  width: 100%;
}

.name-title {
  font-size: 20px;
  margin-bottom: 15px;
}

.name-sub {
  font-size: 15px;
}

.name-desc {
  font-size: 13px;
}

.header- .icon {
  margin-bottom: 2%;
  font-size: 12px;

  cursor: pointer;

  transition: all 0.1s ease;
}

.icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: rgb(222, 255, 155);
  font-weight: bold;
}

@media only all and (max-width: 800px) {
  .name {
    flex-wrap: wrap;
  }

  .links {
    word-break: keep-all;
  }

  .pic-container {
    width: 50px;
    height: 50px;
  }

  .name-title {
    font-size: 10px;
  }

  .name-sub {
    font-size: 8px;
  }

  .name-desc {
    font-size: 3px;
  }

  .header- .icon {
    font-size: 3px;
  }
}

@media only screen and (min-width: 800px) and (max-width: 1600px) {
  .pic-container {
    width: 70px;
    height: 70px;
  }

  .name-title {
    font-size: 15px;
  }

  .name-sub {
    font-size: 12px;
  }

  .name-desc {
    font-size: 8px;
  }

  .header- .icon {
    font-size: 12px;
  }
}

.body-container {
	padding: 0.5%;
	padding-top: 3%;

	width: 100%;
}

.body- {
	margin: 1%;
	width: 100%;
}

.body-1 {
	justify-content: center;
	height: 40%;
}

.body-2 {
	justify-content: center;
	height: 60%;
}

.body-3 {
	height: 63%;
	margin-bottom: 15px;
}

.body-4 {
	height: 32%;
}

@media only screen and (max-width: 800px) {
	.body-1 {
		justify-content: center;
		height: 35%;
	}

	.body-2 {
		justify-content: center;
		height: 55%;

		margin-top: 5%;
	}
}

@media only screen and (min-width: 800px) and (max-width: 1600px) {
	.body-1 {
		height: 38%;
	}

	.body-2 {
		height: 56%;
	}
}

.card-container {
	height: 100%;
	background-color: white;
	border-style: solid;
	border-width: 1px;
	border-radius: 10px;

	transition: all 0.2s ease;
}

.card-container:hover {
	z-index: 100000;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.card-title {
	margin-top: -12px;
	margin-left: 10px;

	width: -webkit-max-content;

	width: -moz-max-content;

	width: max-content;

	font-size: 15px;
	background-color: white;
}

.card-body {
	width: 100%;
	height: 100%;
	padding: 3%;
	padding-top: 0;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.card-body-img {
	width: 80%;
}

.skill-container {
	display: flex;
	flex-direction: row;
}

.skill-title {
	font-size: 12px;
}

.skillBox {
	width: 25px;
	height: 25px;

	margin: 5px;
}

.skillImg {
	width: 100%;
}

.card-project-container {
	width: 100%;
	margin: 5px;

	display: flex;
	flex-flow: wrap;
	justify-content: space-evenly;
}

.card-project-img-container {
	width: 150px;
}

.card-project-text-container {
	width: 100px;
	align-self: center;
}

.card-project-text {
	word-break: keep-all;
	word-wrap: break-word;

	font-size: 18px;
}

.card-project-img {
	width: 100%;
	border-radius: 10px;
	box-shadow: 1px 1px 5px 0.5px grey;
}

.card-ex-container {
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	width: 100%;
}

.card-ex-icon {
	font-size: 80px;

	text-align: center;
}

.card-ex-text {
	font-size: 13px;

	word-wrap: break-word;
	word-break: keep-all;
}

.card-passion-icon-container {
	font-size: 45px;
	width: 100%;

	display: flex;
	align-items: center;
	justify-content: space-evenly;
}

@media only screen and (max-width: 800px) {
	.card-title {
		font-size: 8px;
	}

	.skillBox {
		flex-wrap: wrap;
		width: 8px;
		margin: 0;
	}

	.skill-title {
		font-size: 8px;
	}

	.card-ex-icon {
		font-size: 10px;
	}

	.card-ex-text {
		font-size: 6px;
	}

	.card-passion-icon-container {
		font-size: 10px;
	}

	.card-project-text {
		font-size: 8px;
	}
}

@media only screen and (min-width: 800px) and (max-width: 1600px) {
	.skillBox {
		flex-wrap: wrap;
		width: 15px;
	}

	.card-ex-icon {
		font-size: 30px;
	}

	.card-ex-text {
		font-size: 10px;
	}

	.card-passion-icon-container {
		font-size: 27px;
	}

	.card-project-text {
		font-size: 12px;
	}
}

.ant-modal-body {
	/* background-color: transparent; */
	padding: 0;
	margin-top: 15%;
}

.ant-modal-footer {
	display: none;
}

.ant-modal-close {
	display: none;
}

/* STRENGTH */

.modal-str-img-container {
	width: 100%;
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
}

.modal-str-img {
	width: 100%;
	border-radius: 15px;
}

.modal-skill-container {
	width: 500px;
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);

	background-color: white;
	border-radius: 10px;
}

/* SKILLS */

.modal-skill-img-container {
	display: flex;
	justify-content: center;
	align-items: center;
}

.modal-skill-title {
	font-size: 30px;
	text-align: center;
}

.modal-skillBox {
	width: 50px;
	margin: 2%;
	padding: 5px;
	border-radius: 15px;
	box-shadow: 1px 1px 5px 2px grey;
}

.modal-skillImg {
	width: 100%;
}

/* EXPERIENCE */

.modal-ex-container {
	background-color: white;
	border-radius: 10px;

	width: 55vh;
	height: 60vh;

	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5%;

	-webkit-transform: scale(1.5);

	        transform: scale(1.5);
}

/* project CSS */

.modal-project-container {
	margin-top: -30%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	text-align: center;
}

.modal-project-title {
	font-size: 30px;
	font-weight: bold;
}

.modal-project-1 {
	margin: 5%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.modal-intro-container {
	width: 100px;
	display: flex;
	flex-direction: row;
	align-content: center;
	justify-content: center;
}

.modal-intro-container-2 {
	width: 200px;
}

.modal-intro-img {
	width: 100%;
	border-radius: 10px;
}

.modal-stack-container {
	width: 300px;
}

.modal-stack-img {
	width: 100%;
}

.modal-project-click {
	margin-top: 3%;
	cursor: pointer;
	transition: all 0.2s ease;
}

.modal-project-click:hover {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	color: yellowgreen;
	font-weight: bold;
}

.modal-project-text {
	font-size: 20px;
	margin-top: 10px;
}

/* passion */

.modal-passion-container {
	margin-top: -15%;
	padding: 1%;
	border-radius: 10px;

	width: 90vh;
	background-color: white;
	margin-left: -20%;
}

.modal-passion-content-container {
	margin: 3%;
	width: 100%;
	display: flex;
}

.modal-passion-img-container {
	width: 200px;
}

.modal-passion-img {
	width: 100%;
	border-radius: 10px;
	box-shadow: 1px 1px 4px 1px grey;
}

.modal-passion-text-container {
	margin-left: 4%;

	display: flex;
	flex-direction: column;
	justify-content: center;

	font-size: 18px;
}

@media only all and (max-width: 800px) {
	.modal-str-img-container {
		-webkit-transform: scale(0.8);
		        transform: scale(0.8);
	}

	.modal-skill-container {
		width: 45vh;
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		width: 100%;
	}

	.modal-ex-container {
		width: 100%;
		-webkit-transform: scale(0.7);
		        transform: scale(0.7);
	}

	.timeLineTxt {
		font-size: 20px;
	}

	.modal-passion-container {
		margin: 0;
		margin-top: -15%;
		width: 100%;
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
	}

	.modal-passion-img-container {
		width: 30vh;
	}
}

@media only screen and (min-width: 800px) and (max-width: 1600px) {
}

